body {
    background: #01182c;
    color: #ffffff;
    font-family: 'Quicksand', Arial, sans-serif;
    font-size: 16px;
    height: 100vh;
    margin: 0;
}

p {
    line-height: 1.5em;
}

a {
    text-decoration: none;
}

#root {
    height: 100vh;
}

#app {
    height: 100vh;
}

#badge-container {
    background-color: #e67e22;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    height: 25vh;
    -ms-flex-pack: space-evenly;
        justify-content: space-evenly;
}

.badge-copy-container {
    -ms-flex-align: center;
        align-items: center;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    width: 33%;
}

#badge-name {
    text-shadow: 0 0 0.625em #01182c;
    font-size: 2em;
}

#badge-email {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}

#badge-email-button {
    background: none;
    border: none;
    color: #ffffff;
    font-size: 1em;
    text-shadow: 0 0 0.625em #01182c;
}

#badge-email-button:hover {
    cursor: pointer;
}

#badge-email-button:focus {
    outline: 0;
}

.hero-text {
    color: #ffffff;
    margin: .5em;
}

#profile-pic {
    border-radius: 4.688em;
    -webkit-box-shadow: 0 0 0.938em #01182c;
            box-shadow: 0 0 0.938em #01182c;
    z-index: 2;
}

#profile-pic-anchor {
    z-index: 3;
}

#profile-pic:hover {
    cursor: pointer;
}

#copy-container {
    color: #ffffff;
    font-size: 1.125em;
    height: 55vh;
    overflow: scroll;
    padding: 1em 10em 0 10em;
    text-align: center;
}

.portfolio-link {
    color: #ffffff;
    text-decoration: none;
    text-shadow: 0 0 1em #e67e22;
}

.portfolio-link > p:hover {
    color: #e67e22;
    text-shadow: 0 0 0.938em #e67e22;
    -webkit-transition: all 0.2s ease;
    -o-transition: all 0.2s ease;
    transition: all 0.2s ease;
}

.portfolio-item {
    padding: 25px;
}

.tech-logo {
    margin: 25px .75em;
    font-size: 75px;
}

#horizon-container {
    -ms-flex-align: center;
        align-items: center;
    border-top: 0.075em solid #e67e22;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    height: 15vh;
    -ms-flex-pack: distribute;
        justify-content: space-around;
}

.hover-item {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
}

/* pulse animation for section hovers */
@-webkit-keyframes pulse {
    0%, 100% {
        -webkit-box-shadow: 0 0 0.313em #e67e22;
                box-shadow: 0 0 0.313em #e67e22;
    }
    50% {
        -webkit-box-shadow: 0 0 1.563em #e67e22;
                box-shadow: 0 0 1.563em #e67e22;
    }
}
@keyframes pulse {
    0%, 100% {
        -webkit-box-shadow: 0 0 0.313em #e67e22;
                box-shadow: 0 0 0.313em #e67e22;
    }
    50% {
        -webkit-box-shadow: 0 0 1.563em #e67e22;
                box-shadow: 0 0 1.563em #e67e22;
    }
}

.circle {
    -webkit-animation: pulse 2s ease-in-out infinite;
            animation: pulse 2s ease-in-out infinite;
    background-color: #e67e22;
    border: none;
    border-radius: 50%;
    -webkit-box-shadow: 0 0 0.938em #e67e22;
            box-shadow: 0 0 0.938em #e67e22;
    height: 2.5em;
    margin: 0.313em;
    -webkit-transition: all 0.2s ease-in-out;
    -o-transition: all 0.2s ease-in-out;
    transition: all 0.2s ease-in-out;
    width: 2.5em;
}



